<template>
    <div class="view-wrapper" :style="{minHeight:pageMixin_browser.clientHeight}">
        <DP_Popup
                :display.sync="popup"
                :show-close="true"
        >
            <div class="popup">
                <h5 class="rule_title">奖学金规则</h5>
                <p> 1、将购买成功的课程分享给好友，好友通过你分享的链接或海报，开团或单购</p>
                <p> 2、好友购买成功，你将获得奖学金，暂存在你的奖学金账户中，可累计，不设上限，可随时提现</p>
                <p> 3、奖学金每天只可以提现1次，提现后于24小时内到账，提出现金不能少于2元。如好友退款，将扣除奖学金。如有问题，请咨询客服</p>
            </div>
        </DP_Popup>
        <div class="banner">
            <div class="caidai"></div>
        </div>
        <div class="board">
            <div class="avatar"><img :src="userInfo.avatar" alt="">
            </div>
            <div class="username">{{userInfo.name}}</div>
<!--            <div class="rule" @click="showPopup">奖励规则</div>-->
            <div class="money-wrapper">
                <div class="left">
                    <div class="label">累计奖学金（元）</div>
                    <div class="money dp-ellipsis">
                        <DP_CountUp :decimals="2"
                                    :endnum="wallet.totalAsset"
                                    class="count_up"
                                    duration="1.5"
                                    prefix=""
                                    separator=""
                                    suffix=""
                        ></DP_CountUp>
                    </div>
                </div>
                <div class="middle"></div>
                <div class="right">
                    <div class="label">可提取金额（元）</div>
                    <div class="money dp-ellipsis">
                        <DP_CountUp :decimals="2"
                                    :endnum="wallet.asset"
                                    class="count_up"
                                    duration="1.5"
                                    prefix=""
                                    separator=""
                                    suffix=""
                        ></DP_CountUp>
                    </div>
                </div>
            </div>
            <button class="share-btn " type="button" @click="cashBack">立即提现</button>
        </div>
        <div class="course-list">
            <h5 class="title"> 邀请好友购买以下课程，立赚奖学金</h5>
            <CourseList :pay-user="payUser" :data="courseList"></CourseList>
        </div>
    </div>
</template>

<script>
    // import DP_Dev from '@/components/DP_Dev';
    import DP_CountUp from '@/components/DP_CountUp/index'
    import CourseList from './CourseList/index'
    import DP_Popup from '../../components/DP_Popup/index'
    import {mapGetters} from "vuex";
    import {checkUserType} from "@/api/parameter";
    import {sernsorFn} from "@/utils/sensors";
    import pageMixin from "@/mixin/pageMixin";
    export default {
        metaInfo() {
            return {
                title: this.$route.meta.title, // set a title
                meta: [{                 // set meta
                    name: 'keywords',
                    content:  this.$route.meta.title+",熊猫博士"
                }],
            }
        },
        name: "account-center",
        components: {
            DP_CountUp,
            CourseList,
            DP_Popup,
        },
        props: {},
        mixins: [pageMixin],
        data() {
            return {
                popup: false,
                payUser: false,//付费用户,
                courseList: [
                    {
                        id: 1,
                        title: '熊猫博士识字',
                        desc: '科学成长，天天向上！',
                        money: 8.8,
                        cover: require('./CourseList/asssets/Icon_BigCorner_1024.png'),
                        price: 8.8,
                        chapter: 10
                    },
                ]
            }
        },
        watch: {},
        computed: {
            ...mapGetters({
                userInfo: "getUserInfo",
            }),
            ...mapGetters({
                wallet: "getWallet",
            }),
        },
        methods: {
            showPopup() {
                this.popup = true;
            },
            closePopup() {
                this.popup = false;
            },
            // getInfo() {
            //     getWalletInfo().then((res) => {
            //         const {data: {code, data}} = res;
            //         if (code === 0) {
            //             data.totalAsset = data.totalAsset ? numeral(data.totalAsset).divide(100).value() : 0;
            //             data.asset = data.asset ? numeral(data.asset).divide(100).value() : 0;
            //             console.log(data, "qian")
            //             this.$store.commit('setWallet', data);
            //         }
            //     })
            // }
            cashBack(){
                sernsorFn('buttonClick', {
                    tabTitle: document.title,
                    moduleTitle: 'Referral',
                    buttonType: "普通按钮",
                    buttonName: "立即提现"
                });

                this.dp_go('cash-back')
            },
            async getData() {
                // eslint-disable-next-line no-unused-vars
                const user = await this.checkUser();
                // const list = await this.getCourseList();
            },
            async checkUser() {
                const res = await checkUserType("com.drpanda.chineseacademy");
                const {data: {data, code, message}} = res;
                return new Promise((resolve, reject) => {
                    if (code === 0) {
                        if (data) {
                            this.payUser = true;
                            resolve(true);
                        } else {
                            resolve(false);
                        }
                    } else {
                        reject(message)
                    }

                })
            },
            getCourseList() {

            }
        }
        ,
        created() {
            // this.getInfo();
            this.getData();
            this.$store.dispatch('getWallet');
            console.log(this.$route, 111111111);
        }
        ,
        mounted() {
        }
        ,
    }
</script>

